@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
.logo {
  font-family: Poppins;
}
.log-in-to-your-account,
.logo {
  position: relative;
  line-height: 125.3%;
  font-weight: 600;
  display: inline-block;
}
.welcome-back-please-enter-you {
  position: relative;
  font-size: 16px;
  line-height: 24px;
  font-family: Poppins;
  color: rgba(26, 41, 61, 0.83);
  display: inline-block;
}
.log-in-headline {
  width: 270px;
  height: 53px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4px;
}
.github-icon,
.google-g-logo-1-icon {
  position: relative;
  width: 30.52px;
  height: 33.14px;
  flex-shrink: 0;
  object-fit: cover;
}
.google-g-logo-1-icon {
  width: 25px;
  height: 25px;
}
.continue-with-google {
  border-radius: 4px;
  width: 336px;
  height: 56px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  padding: 8px 14px;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.or {
  position: absolute;
  top: 0;
  left: 143px;
  line-height: 134%;
  display: inline-block;
}
.line-div,
.line-div1 {
  position: absolute;
  top: 10.5px;
  left: -0.5px;
  width: 127px;
  height: 1px;
}
.line-div1 {
  left: 181.5px;
  border-top: 1px solid rgba(28, 52, 84, 0.26);
  box-sizing: border-box;
}
.line-div{
  border-top: 1px solid rgba(28, 52, 84, 0.26);
  box-sizing: border-box;
}
.or-seperator {
  position: relative;
  width: 308px;
  height: 19px;
  flex-shrink: 0;
  text-align: center;
  color: rgba(28, 52, 84, 0.26);
}
.field {
  position: absolute;
  top: 19px;
  left: 0;
  border-radius: 3px;
  width: 336px;
  height: 57px;
}
.email-address1,
.rishabhsingh {
  position: absolute;
  font-weight: 500;
}
.rishabhsingh {
  width: calc(100% - 179px);
  top: 36px;
  left: 15px;
  line-height: 22px;
  display: inline-block;
}
.email-address1 {
  width: calc(100% - 145px);
  top: 0;
  left: 1px;
  font-size: 12px;
  color: rgba(27, 43, 65, 0.72);
  display: flex;
  align-items: center;
  height: 14px;
}
.email-address {
  position: relative;
  width: 336px;
  height: 76px;
  flex-shrink: 0;
}
.field1 {
  font-size: 17px;
  position: absolute;
  top: 19px;
  left: 0;
  border-radius: 3px;
  border: 1px solid rgba(28, 55, 90, 0.16);
  box-sizing: border-box;
  width: 336px;
  height: 57px;
}
.eye-icon {
  position: absolute;
  top: 35px;
  right: 16px;
  width: 24px;
  height: 24px;
  overflow: hidden;
}
.div,
.password1 {
  position: absolute;
  font-weight: 500;
}
.div {
  width: calc(100% - 179px);
  top: 35px;
  left: 16px;
  line-height: 22px;
  display: inline-block;
}
.password1 {
  width: calc(100% - 145px);
  top: 0;
  left: 1px;
  font-size: 12px;
  font-family: Poppins;
  color: rgba(27, 43, 65, 0.72);
  display: flex;
  align-items: center;
  height: 14px;
}
.checkbox-icon,
.password {
  position: relative;
  flex-shrink: 0;
}
.password {
  width: 336px;
  height: 76px;
  font-family: Poppins;
}
.checkbox-icon {
  width: 18px;
  height: 18px;
  object-fit: cover;
}
.checkbox,
.remember-me1 {
  position: absolute;
  display: flex;
}
.checkbox {
  top: 1px;
  left: 0;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.remember-me1 {
  top: 4px;
  left: 27px;
  line-height: 122%;
  align-items: center;
  width: 98px;
  height: 18px;
}
.remember-me {
  position: relative;
  width: 125px;
  height: 19px;
  flex-shrink: 0;
}
.forgot-password {
  position: relative;
  font-weight: 600;
  color: #333;
  text-align: center;
  display: inline-block;
}
.remember-me-forgot-password {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 80px;
  font-size: 13px;
  color: rgba(27, 43, 65, 0.72);
}
.input-section {
  width: 336px;
  height: 323px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
  text-align: left;
  font-size: 14px;
  color: rgba(24, 39, 58, 0.94);
}
.button,
.icon {
  position: relative;
}
.icon {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  overflow: hidden;
  display: none;
}
.button {
  font-weight: 600;
  display: inline-block;
}
.buttons,
.log-in {
  border-radius: 20px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.log-in {
  background-color: rgba(21, 32, 43, 0.8);
  box-shadow: 0 5px 4px rgba(0, 0, 0, 0.25);
  width: 336px;
  height: 56px;
  flex-direction: row;
  padding: 8px 14px;
  box-sizing: border-box;
  gap: 4px;
}
.buttons {
  background-color: rgba(63, 39, 19, 0.8);
  width: 335px;
  height: 59px;
  flex-direction: column;
  font-size: 16px;
  color: #fff;
  font-family: Poppins;
}
.dont-have-an-account1,
.sign-up {
  position: relative;
  display: inline-block;
}
.dont-have-an-account1 {
  line-height: 122%;
}
.sign-up {
  font-weight: 800;
  color: rgba(34, 51, 68, 0.8);
  width: 100px;
}
.dont-have-an-account {
  width: 60%;
  height: 17px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 2px;
  font-size: 13px;
}
.frame-div,
.login,
.login1 {
  display: flex;
  align-items: center;
}
.frame-div {
  flex-direction: column;
  justify-content: flex-start;
  gap: 30px;
}
.login,
.login1 {
  overflow: hidden;
  flex-direction: row;
  box-sizing: border-box;
  justify-content: center;
}
.log-in:hover{
  background-color: rgba(21, 32, 43, 0.8);
  box-shadow: 0 5px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}
.log-in:active{
  background-color: rgba(21, 32, 43, 0.8);
  box-shadow: 0 5px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}
.login1 {
  max-width: 450px;
  border-radius: 30px;
  background-color: #fff;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 59px 113px;
}
.login {
  position: relative;
  background-color: #15202b;
  width: 100%;
  padding: 121px 247px;
  text-align: center;
  font-size: 22px;
  color: #000;
  font-family: Poppins;
}
