@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
.logo {
  position: relative;
  line-height: 125.3%;
  font-weight: 600;
  display: inline-block;
}
.sign-up-now-to-get-started-wit {
  position: relative;
  font-size: 14px;
  line-height: 134%;
  color: rgba(26, 41, 61, 0.83);
  display: inline-block;
}
.log-in-headline {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4px;
}
.image-2-icon {
  width: 30.52px;
  height: 33.14px;
  object-fit: cover;
}
.google-g-logo-1-icon,
.icon,
.image-2-icon {
  position: relative;
  flex-shrink: 0;
}
.google-g-logo-1-icon {
  width: 19px;
  height: 19px;
  object-fit: cover;
}
.icon {
  width: 20px;
  height: 20px;
  overflow: hidden;
  display: none;
}
.continue-with-google {
  border-radius: 4px;
  border: 1px solid rgba(28, 52, 84, 0.26);
  box-sizing: border-box;
  width: 336px;
  height: 56px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  padding: 8px 14px;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.or {
  position: absolute;
  top: 0;
  left: 140px;
  line-height: 134%;
  display: inline-block;
}
.line-div,
.line-div1 {
  position: absolute;
  top: 10.5px;
  left: -0.5px;
  border-top: 1px solid rgba(28, 52, 84, 0.26);
  box-sizing: border-box;
  width: 127px;
  height: 1px;
}
.line-div1 {
  left: 181.5px;
}
.or-seperator {
  position: relative;
  width: 308px;
  height: 19px;
  flex-shrink: 0;
  text-align: center;
  font-size: 14px;
  color: rgba(28, 52, 84, 0.26);
}
.rectangle-div,
.rectangle-icon {
  position: relative;
  border-radius: 3px;
  flex-shrink: 0;
}
.rectangle-icon {
  width: 153px;
  height: 57px;
}
.rectangle-div {
  align-self: stretch;
  background-color: #fff;
  border: 1px solid #dfdfdf;
  box-sizing: border-box;
  width: 154px;
}
.frame-div {
  position: absolute;
  top: 15px;
  left: 0;
  width: 334px;
  height: 57px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 27px;
}
.first-name {
  width: 162px;
  font-weight: 500;
  display: inline-block;
}
.frame-div1 {
  position: absolute;
  top: 0;
  left: 0.5px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 20px;
}
.name {
  position: relative;
  width: 334px;
  height: 72px;
  flex-shrink: 0;
  color: #5b6676;
}
.field {
  position: absolute;
  top: 19px;
  left: 0;
  border-radius: 3px;
  border: 1px solid rgba(28, 55, 90, 0.16);
  box-sizing: border-box;
  width: 336px;
  height: 57px;
}
.span {
  color: #ff4238;
}
.email-address-txt-container {
  line-break: anywhere;
  width: 100%;
}
.email-address {
  position: relative;
  width: 336px;
  height: 76px;
  flex-shrink: 0;
}
.eye-disabled-icon,
.eye-disabled-icon1,
.eye-icon {
  position: absolute;
  width: 24px;
  height: 24px;
  overflow: hidden;
}
.eye-icon {
  top: 35px;
  right: 16px;
}
.eye-disabled-icon,
.eye-disabled-icon1 {
  top: 303px;
  left: 296px;
  opacity: 0;
}
.eye-disabled-icon1 {
  top: 395px;
}
.span2 {
  letter-spacing: -0.22em;
}
.conf-irm-password {
  position: absolute;
  width: calc(100%);
  top: 0;
  left: 1px;
  font-weight: 500;
  display: flex;
  align-items: center;
  height: 14px;
}
.input-section {
  align-self: stretch;
  height: 417px;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
  text-align: left;
  font-size: 12px;
  color: rgba(27, 43, 65, 0.72);
}
.checkbox-icon {
  position: relative;
  width: 18px;
  height: 18px;
  flex-shrink: 0;
}
.terms-of-service {
  text-decoration: underline;
  color: rgba(21, 32, 43, 0.8);
}
.agree-to-tc {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 11px;
  text-align: left;
  font-size: 12px;
  color: rgba(27, 43, 65, 0.72);
}
.button {
  position: relative;
  font-weight: 600;
  display: inline-block;
}
.buttons,
.get-started {
  display: flex;
  align-items: center;
  justify-content: center;
}
.get-started {
  border-radius: 4px;
  background-color: rgba(21, 32, 43, 0.8);
  width: 336px;
  height: 56px;
  flex-shrink: 0;
  flex-direction: row;
  padding: 8px 14px;
  box-sizing: border-box;
  gap: 4px;
}
.get-started:hover {
  background-color: rgba(21, 32, 43, 0.9);
  cursor: pointer;
}
.buttons {
  border-radius: 20px;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.25);
  flex-direction: column;
  font-size: 16px;
  color: #f6f6f6;
}
.already-have-an-account1,
.log-in {
  position: relative;
  display: inline-block;
}
.already-have-an-account1 {
  line-height: 122%;
}
.log-in {
  font-weight: 600;
  color: rgba(21, 32, 43, 0.8);
  cursor: pointer;
}
.already-have-an-account {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 2px;
  font-size: 13px;
}
.desktop-2,
.login,
.register {
  overflow: hidden;
  display: flex;
  box-sizing: border-box;
  justify-content: center;
}
.register {
  background-color: #fff;
  flex-direction: column;
  padding: 27px 25px;
  align-items: center;
  gap: 25px;
}
.desktop-2,
.login {
  flex-direction: row;
}
.login {
  max-width: 450px;
  border-radius: 30px;
  background-color: #fff;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 0 52px;
  align-items: flex-start;
}
.desktop-2 {
  position: relative;
  background-color: #15202b;
  width: 100%;
  padding: 90px 230px;
  align-items: center;
  text-align: center;
  font-size: 22px;
  color: #000;
  font-family: Poppins;
}
