.javascript-b {
  position: relative;
  display: inline-block;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  z-index: 0;
  width: 81px;
  height: 19px;

  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 19px;
  /* identical to box height */

  display: flex;
  align-items: center;

  color: #ffffff;

  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.drop-down-language {
  position: absolute;
  margin: 0 !important;
  top: 14.22px;
  right: 7.94px;
  width: 12.03px;
  height: 5.56px;
  flex-shrink: 0;
  object-fit: cover;
  z-index: 1;
}
.select-language-div {
  border-radius: 5px;
  background-color: #474973;
  width: 122px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  padding: 9px;
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  gap: 7px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  color: #ffffff;
  z-index: 0;
  border: none;
    font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
}
.run-f5-b {
  position: relative;
  display: inline-block;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 94px;
  height: 19px;

  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 19px;
  /* identical to box height */

  display: flex;
  align-items: center;

  color: #ffffff;
}
.run-code-icon {
  position: relative;
  width: 12px;
  height: 12px;
  flex-shrink: 0;
  object-fit: cover;
}
.run-code-div,
.top-navbar-left {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.run-code-div {
  border-radius: 5px;
  background-color: #474973;
  padding: 8px;
  box-sizing: border-box;
  gap: 9px;
  transition-duration: .3s;
  border: none;
}

.run-code-div:hover{
  cursor: pointer;
  background-color:#686caa;
}


.run-code-div:active{
  background-color:#232438;
  
}

.top-navbar-left {
  align-self: stretch;
  /* width: 669px; */
  flex-shrink: 0;
  gap: 14px;
}
.settings-icon {

  position: relative;
  width: 25px;
  height: 25px;
  flex-shrink: 0;
  object-fit: cover;
}
.settings-icon:hover{
  cursor:pointer;
}
.am-div,
.top-navbar-icon {
  display: flex;
  align-items: center;
  justify-content: center;
}
.top-navbar-icon {
  flex-direction: row;
  padding: 2px 3px;
  box-sizing: border-box;
}
.am-div {
  position: relative;
  font-weight: 500;
  width: 23px;
  height: 21px;
  flex-shrink: 0;
  z-index: 0;
}
.ellipse-1-scaled-raster-imag {
  position: relative;
  width: 6px;
  height: 6px;
  flex-shrink: 0;
  object-fit: cover;
}
.component-6-div,
.frame-div {
  display: flex;
  flex-direction: row;
  padding: 2px;
  box-sizing: border-box;
}
.frame-div {
  margin: 0 !important;
  position: absolute;
  top: 18.5px;
  left: 22px;
  border-radius: 14px;
  background-color: #fff;
  align-items: flex-end;
  justify-content: flex-end;
  z-index: 1;
}
.component-6-div {
  border-radius: 42px;
  background: linear-gradient(180deg, #ced2da, #abafb7);
  width: 30px;
  height: 29px;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  position: relative;
  gap: 0;
}
.vector-scaled-raster-image {
  position: relative;
  width: 9px;
  height: 4.25px;
  flex-shrink: 0;
  object-fit: cover;
}
.icon-div,
.online-and-profile {
  display: flex;
  align-items: center;
}
.icon-div {
  flex-direction: column;
  padding: 4px 2px;
  box-sizing: border-box;
  justify-content: center;
}
.online-and-profile {
  flex-direction: row;
  justify-content: flex-start;
  gap: 4px;
}
.top-nav-right,
.top-navbar-div {
  align-self: stretch;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
}
.top-nav-right {
  position: relative;
  padding: 15px 9px;
  justify-content: flex-end;
  gap: 34px;
  text-align: center;
  font-size: 12px;
  font-family: Farro;
}
.top-navbar-div {
  background-color: #234;
  height: 59px;
  padding: 7px 0 7px 10px;
  justify-content: space-between;
}
.vector-scaled-raster-image1 {
  position: relative;
  width: 18.5px;
  height: 15.45px;
  flex-shrink: 0;
  object-fit: cover;
}
.frame-div1,
.left-navbar-div {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.frame-div1 {
  padding: 10px;
  box-sizing: border-box;
  justify-content: center;
}
.left-navbar-div {
  background-color: #234;
  width: 50px;
  flex-shrink: 0;
  justify-content: flex-start;
}
.ide-div {
  align-self: stretch;
  flex: 1;
  background-color: #15202b;
  justify-content: unset;
}
.ide-and-side-bar {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 0;
}
.entire-frame-div,
.online-compiler-div {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.entire-frame-div {
  align-self: stretch;
  flex: 1;
  justify-content: flex-start;
  gap: 0;
}
.online-compiler-div {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 10000;
  color: #fff;
}
.dot-span {
  position: relative;
  width: 6px;
  height: 6px;
  flex-shrink: 0;
  border-radius: 50%;
  background-color: #00ff00;
}

@media only screen and (max-width: 432px) {
  .top-nav-right {
    display: none;
  }
}

#editor {
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  height: 63%; /*63%/30rem for open div 95% for closed*/
  width: 100%;
  margin-bottom: 0 !important;
}
