.console{
  width: 100%;
  height:37%;
  background-color: #15202B;
  display: flex;
  flex-direction: column;
}
.console-upper{
  background-color: #223344;
  height: 4.2rem;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  flex-direction: row;
  padding-right: 1rem;

}

.console-upper-right{
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #8C8C8C;
  height:72%;
  width:auto;
  padding-bottom: .5rem;
  font-weight: bolder;
  padding-left: 1rem;
}


.buttons{
  display: flex;
  flex-direction: row;
  padding: 0.1rem 0 0 1rem;
}
.input-button{
  padding:.5rem;
  text-align: left;
  background-color: #474973;
  border-radius: 0.5rem 0.5rem 0 0;
  color: #8C8C8C;
  border: none;
  width: 10%;
  font-weight: bolder;
  font-size: 1rem;
}
.output-button{
  padding:.5rem;
  text-align: left;
  background-color: #223344;
  border-radius: 0.5rem 0.5rem 0 0;
  color: #8C8C8C;
  border: none;
  width: 10%;
  font-weight: bolder;
  font-size: 1rem;
  cursor: pointer;
}
.output-button:hover{
  background-color: #474973;
}
.text-area{
  width: auto;
  height: 100%;
  background-color: #223344;
  color: white;
  border: none;
  margin: auto;
  margin:0rem 1rem 1rem 1rem;
  border-radius: 0 0.5rem 0.5rem 0.5rem;
}
#inputEditor{
  height:100%;
  width:100%;
}