.editor-settings-div1,
.icon-cancel {
  position: relative;
  flex-shrink: 0;
}
.editor-settings-div1 {
  font-weight: 800;
  display: flex;
  align-items: center;
  width: 211px;
  height: 64px;
}
.icon-cancel {
  width: 21.83px;
  height: 20.67px;
  object-fit: cover;
}
.icon-cancel:hover{
  cursor:pointer;
}
.auto-complete,
.top-frame-div {
  display: flex;
  align-items: center;
}
.top-frame-div {
  align-self: stretch;
  background-color: #234;
  overflow: hidden;
  flex-direction: row;
  padding: 0 17px;
  box-sizing: border-box;
  justify-content: space-between;
}
.auto-complete {
  position: relative;
  width: 204px;
  height: 40px;
  flex-shrink: 0;
}
.javascript-b {
  position: relative;
  display: inline-block;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  -webkit-text-stroke: 1px #9a9c9e;
  z-index: 0;
}
.drop-down-language {
  position: absolute;
  margin: 0 !important;
  top: 10.22px;
  right: 7.94px;
  width: 12.03px;
  height: 5.56px;
  flex-shrink: 0;
  object-fit: cover;
  z-index: 1;
}
.auto-compelete-dropdown-div,
.auto-complete-div,
.editor-setting-div {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
}
.auto-compelete-dropdown-div {
  border-radius: 5px;
  background-color: #474973;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 19px;
  color:#ffffff;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  --webkit-text-stroke: 1px #9a9c9e;
  width: 18%;
  height: 23px;
  flex-shrink: 0;
  justify-content: flex-start;
  position: relative;
  gap: 7px;
  font-size: 14px;
}
.auto-complete-div,
.editor-setting-div {
  align-self: stretch;
  overflow: hidden;
  padding: 0 31px;
  justify-content: space-between;
}
.editor-setting-div {
  flex-direction: column;
  padding: 0px 17px;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: 20px;
}
.save-b {
  position: relative;
  display: flex;
  align-items: center;
  width: 40px;
  flex-shrink: 0;
  -webkit-text-stroke: 1px #9a9c9e;
}
.lower-settings-div,
.save-settings-div {
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
}
.save-settings-div {
  border-radius: 5px;
  background-color: #474973;
  border: 1px solid #000;
  width: 60px;
  padding: 8px;
  justify-content: center;
}
.save-settings-div:hover{
  cursor:pointer;
}
.lower-settings-div {
  align-self: stretch;
  background-color: #234;
  height: 54px;
  overflow: hidden;
  padding: 0 17px;
  justify-content: flex-end;
  font-size: 14px;
}
.editor-settings-div {
  position: relative;
  background-color: #15202b;
  width: 40%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 0;
  text-align: left;
  font-size: 26px;
  color: #fff;
  font-family: "Open Sans";
  border-radius: .4rem;
  animation: fromTop 0.3s linear;
}

@keyframes fromTop {
  0% {
    transform: translateY(-20%);
  }
  100% {
    transform: translateY(0%);
  }
}

.editor-settings-modal{
  position: fixed;
  left : 0;
  right : 0;
  bottom : 0;
  top : 0;
  display : flex;
  align-items : center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.25);
  z-index: 1000;
}